// @import "./discordant-assets/fonts/nunito/400.css";

/* nunito-cyrillic-ext-400-normal*/
@font-face {
  font-family: "Nunito";
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src: url("./discordant-assets/fonts/nunito/files/nunito-cyrillic-ext-400-normal.woff2")
      format("woff2"),
    url("./discordant-assets/fonts/nunito/files/nunito-all-400-normal.woff")
      format("woff");
  unicode-range: U+0460-052F, U+1C80-1C88, U+20B4, U+2DE0-2DFF, U+A640-A69F,
    U+FE2E-FE2F;
}
