@charset "UTF-8";
html {
  font-family: "Courier";
  font-size: 9pt;
  background-color: white;
  color: black;
  word-wrap: break-word;
  margin: 0;
  padding: 3px 4px 10px 4px;
}

body {
  margin: 0;
  padding: 0;
}

img {
  border: 1px solid #aaa;
  vertical-align: top;
}

object {
  vertical-align: top;
}

hr {
  margin: 0.5em 2em;
}

.line {
  margin: 0 -4px;
  padding: 0 4px 1px 4px;
  clear: both;
}

.line[_type=action] .sender:before {
  content: "• ";
  white-space: nowrap;
}

.inlineimage {
  margin: 10px 0 15px 40px;
  max-width: 200px;
  max-height: 150px;
  -webkit-box-shadow: 2px 2px 2px #888;
}

.avatar {
  display: inline;
  max-width: 24px;
  max-height: 24px;
  margin-right: 3px;
  vertical-align: middle;
}

.url {
  word-break: break-all;
}

.address {
  text-decoration: underline;
